import React, { useEffect } from 'react'
import classNames from 'classnames/bind'
import { useSelector } from 'react-redux'
import { getQueryVariable, getQueryToObject } from '@/utils/util'
import { platformName } from '@/utils/gameUtil.js'
import { dialog } from '@/components/dialog/Dialog'
import Rotate from '@/components/loading/Rotate'
import scss from './loadingGame.module.scss'

const cx = classNames.bind(scss)

export default function LoadingGame() {
  const gameCode = getQueryVariable('gameCode')
  const loginStatus = useSelector(state => !!state.customer.loginName)
  console.log(getQueryToObject())
  useEffect(() => {
    const timer = setTimeout(() => {
      dialog({
        message: '进入游戏错误，请稍后再试',
        callback() {
          window.close()
        }
      })
    }, 2000)

    return () => clearTimeout(timer)
  }, [])

  return (
    <div className={cx('load')}>
      <div className={cx('load__content')}>
        <h1 className={cx('load__title')}>温馨提示</h1>
        <p className={cx('load__text')}>
          {
            `您正在${loginStatus ? '' : '使用试玩身份'}进入 ${platformName[gameCode]}`
          }
        </p>
        <div className={cx('load__rotate')}>
          <Rotate/>
        </div>
      </div>
    </div>
  )
}
